:root {
    --background-color: #f9baba;
    --board-background-color: bisque;
    --board-border-color: rgb(188, 153, 107);
    --panel-background-color: rgb(249, 249, 195);
    --panel-border-color: burlywood;

    --player-no-player: white;
    --player-player1: #99627A;
    --player-player2: #FFD966;
}

body{
    background-color: var(--background-color);
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.app-container {
    width: 800px;
    height: 800px;
    position: absolute;
    left: 50%;
    top: 10%;
    margin-left: -400px;
}

.panel {
    position: absolute;
    background-color: lightgoldenrodyellow;
    border: 8px solid var(--panel-border-color);
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
    box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
    z-index: 1;
}

.header {
    left: 50%;
    top: 90px;
    height: 80px;
    width: 300px;
    margin-left: -120px;
}

.header-text {
    font-size: 29px;
    font-weight: bold;
    width: 220px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.footer {
    left: 50%;
    top: 750px;
    height: 80px;
    width: 300px;
    margin-left: -120px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.footer button {
    position: relative;
    left: 50%;
    top: 20%;
    width: 150px;
    height: 50px;
    background-color: #f5e3e3;
    font-size: 20px;
    -webkit-box-shadow: 3px 5px 10px 2px rgba(0, 0, 0, 0.9);
    box-shadow: 3px 5px 10px 2px rgba(0, 0, 0, 0.9);
    border: 5px solid #ff9f9f;
    font-weight: bold;
}

.footer button:hover{
    background-color: #f98888;
}

.gameBoard{
        display: grid; 
        grid-template-columns: repeat(4, 1fr); 
        grid-template-rows: repeat(4, 1fr); 
        padding: 20px; 
        width: 520px;
        position: absolute;
        left: 50%;
        margin-left: -250px;
        top: 20%;
        background-color: var(--board-background-color);
        border: 8px solid var(--board-border-color);
        border-radius: 8%;
        -webkit-box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
        box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
}

.gameCircle{
        width: 100px; 
        height: 100px; 
        margin: 10px; 
        border-radius: 50% ;
        border: 5px solid black;
}

.player_0{
    background-color: var(--player-no-player);
}

.player_1{
    background-color: var(--player-player1);
}

.player_2{
    background-color: var(--player-player2) ;
}